.popup-about-book__backdrop {
  width: 100%;
  height: 100vh;
  visibility: hidden;
  z-index: 1000;
  opacity: 0;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.is-active {
  visibility: visible;
  opacity: 1;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.popup-about-book__window {
  width: 336px;
  background-color: #fff;
  border: 1px solid #111;
  border-radius: 20px;
  margin: 70px 15px 30px;
  padding: 40px 25px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .popup-about-book__window {
    width: 579px;
    padding: 40px;
  }
}

html.dark .popup-about-book__window {
  background-color: #202024;
  border-color: #fff;
}

.popup-about-book__render-book-container {
  gap: 24px;
}

@media screen and (max-width: 767px) {
  .popup-about-book__render-book-container {
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .popup-about-book__render-book-container {
    display: flex;
  }
}

.popup__book-image {
  object-fit: contain;
  width: 287px;
  align-items: stretch;
  margin-bottom: 14px;
}

